import React from 'react';
import Latex from 'react-latex';
export default function DecomposicaoEspectral(){ return(<>

<h2>
    Decomposição Espectral
</h2>

Antes de seguir para a decomposição Espectral temos que definir alguns componentes

<Latex displayMode={true}>{'$$'+
'{\\vec{q}}_{j} = \\frac{\\vec{x}_j}{||\\vec{x}_j||} \\quad|\\quad'+
'{Q}_{nxn}=\\begin{pmatrix} {\\vec{q}}_{1} & \\dots & {\\vec{q}}_{n}\\end{pmatrix} \\quad|\\quad'+
'{\\Lambda}_{nxn}=\\mathbf{I}\\vec{\\lambda} \\quad|\\quad'+
'\\vec{\\lambda}='+
'\\begin{pmatrix} '+
'\\lambda_1 \\\\ '+
'\\vdots \\\\ '+
'\\lambda_n '+
'\\end{pmatrix}'
+'$$'}</Latex>

Sendo que <Latex>{"$\\{\\lambda_1,...,\\lambda_n\\}\\in\\mathbb{R}$"}</Latex> são os autovalores de <Latex>{"$A\\vec{x}_j$"}</Latex> ordenados e <Latex>{"$\\vec{x}_j$"}</Latex> é o j-ésimo autovetor. Dado essas coisa, temos que

<Latex displayMode={true}>{'$$'+
'AQ= \\begin{pmatrix} A{\\vec{q}}_{1} & \\dots & A{\\vec{q}}_{n}\\end{pmatrix} ='+
'\\begin{pmatrix} {\\lambda_1\\vec{q}}_{1} & \\dots & {\\lambda_n\\vec{q}}_{n}\\end{pmatrix} ='+
'Q\\Lambda \\Rightarrow '+
'A=Q\\Lambda {Q}^{-1}'
+'$$'}</Latex>

<b>Teorema :</b> Se A é simétrica, Q é ortogonal
<br />
<b>Prova:</b>
<Latex displayMode={true}>{'$$'+
'{A}^{T} = {(Q\\Lambda {Q}^{-1})}^T = {{Q}^{-1}}^{T}{\\Lambda}^{T}{Q}^{T} = Q\\Lambda{Q}^{T} = A'
+'$$'}</Latex>
Ou seja, para que <Latex>{"$A={A}^{T}$"}</Latex>, Q tem que ser ortogonal
</>)}